    },

    data() {
    return {
        selectedParcelNameG: this.$store.state.selectedParcelNameG,
        selectedParcelNameD: this.$store.state.selectedParcelNameD,

        features:["height","thickness","density","leafWallArea"],

        selectedFeature:"height",

        
        
        
        }
