                feature: this.selectedFeature,
                parcelNameG : this.selectedParcelNameG,
                parcelNameD : this.selectedParcelNameG
            }
        }
    },

    created() {
        if (!this.$store.getters.getDataIsLoaded) {
            this.$router.push("/");
